@import "../../../css/module/fn";
@import "../../../css/module/color";

.atom {
	@include shadow();
	width: p2m(670);
	//height: p2m(350);
	background-color: #fff;
	border-radius: p2m(15);
	margin: p2m(50) auto 0;
	
	> .news {
		padding: p2m(32);
		
		> h1 {
			font-size: p2m(26);
			font-weight: 900;
			color: $red;
		}
		> time {
			font-size: p2m(20);
			color: #aaa;
		}
		> p {
			@include lineClip;
			font-size: p2m(22);
			color: #333;
		}
	}
	
	> .btnLink {
		background-color: $red;
		color: #fff;
		font-size: p2m(24);
		height: 1rem;
		width: 100%;
		border:{
			bottom-left-radius: p2m(15);
			bottom-right-radius: p2m(15);
		}
	}
}

.detail{
	@include shadow();
	width: p2m(670);
	background-color: #fff;
	border-radius: p2m(15);
	margin: p2m(50) auto;
	padding: p2m(50) p2m(45);
	
	> h1 {
		font-size: p2m(32);
		font-weight: 900;
		color: $red;
	}
	> time {
		font-size: p2m(22);
		color: #aaa;
	}
	> p {
		font-size: p2m(24);
		color: #333;
	}
}